 * @Description: 部门管理
 * @Autor: YML
 * @Date: 2021-05-08 09:34:31
 * @LastEditors: Zhongyu
 * @LastEditTime: 2021-05-19 09:28:44
-->
<template>
  <div class="management-menus">
    <box>
      <!-- 新增按钮 start -->
      <a-button class="operation-button add" @click="addMessage">
        <a-icon type="form" />新增
      </a-button>
      <!-- 新增按钮 end -->
      <!-- 表格展示 start -->
      <div class="contents">
        <a-table
          class="default-line-table department-table"
          :bordered="true"
          :columns="columns"
          :data-source="data"
          :pagination="false"
          :expandIcon="customExpandIcon"
          :scroll="{ y: 'calc(90vh - 12.98vw)' }"
        >
          <span slot="operation" class="operation">
            <div class="count" @click="getMessage()">
              <a-icon type="aliwangwang" />
              <a>所属角色</a>
            </div>
            <div class="count" @click="editMessage()">
              <a-icon type="form" />
              <a>编辑</a>
            </div>
            <div class="count" @click="getMessage()">
              <a-icon type="aliwangwang" />
              <a>查看</a>
            </div>
            <div class="count" @click="deleteMessage()">
              <a-icon type="close" />
              <a>删除</a>
            </div>
          </span>
        </a-table>
      </div>
      <!-- 表格展示 end -->
      <!-- 分页 start -->
      <!-- <div class="pagination">
        <pagination />
      </div> -->
      <!-- 分页 end -->
    </box>
    <!-- 新增弹窗 start -->
    <editmodal
      :title="'新增信息'"
      :visible="addModal"
      @close="closeModal"
    ></editmodal>
    <!-- 新增弹窗 end -->
    <!-- 编辑弹窗 start -->
    <editmodal
      :title="'编辑信息'"
      :visible="editModal"
      @close="closeModal"
    ></editmodal>
    <!-- 编辑弹窗 end -->
    <!-- 查看成员弹窗 start -->
    <!-- <memberList :visible="examineModal" @close="closeModal"></memberList> -->
    <!-- 查看成员弹窗 end -->
    <!-- 删除弹窗 start -->
    <deletemodal :visible="deleteModal" @close="closeModal"></deletemodal>
    <!-- 删除弹窗 end -->
  </div>
</template>

<script>
import Box from "../../components/Box";
import editmodal from "./editModal";
import deletemodal from "./deleteModal";
export default {
  components: {
    Box, //边框组件
    editmodal, //新增、编辑弹窗
    deletemodal //删除弹框
  },
  data() {
    return {
      columns: [
        {
          title: "菜单名称",
          dataIndex: "name",
          key: "name",
          class: "unfold",
          width: "10%"
        },
        {
          title: "上级菜单名称",
          dataIndex: "code",
          key: "code",
        },
        {
          title: "图标",
          dataIndex: "division",
          key: "division",
        },
        {
          title: "菜单类别",
          dataIndex: "phone",
          key: "phone",
        },
        {
          title: "菜单URL",
          dataIndex: "address",
          key: "address",
        },
        {
          title: "打开方式",
          dataIndex: "openType",
          key: "openType",
        },
        {
          title: "状态",
          dataIndex: "status",
          key: "status",
        },
        {
          title: "操作",
          dataIndex: "operation",
          key: "operation",
          width: "25%",
          scopedSlots: { customRender: "operation" }
        }
      ], //表头信息
      data: [], //表格内容
      selectedRowKeys: [], // 列表项是否可选择
      addModal: false, //新增操作弹窗是否显示
      editModal: false, //编辑操作弹窗是否显示
      examineModal: false, //查看操作弹窗是否显示
      deleteModal: false //删除操作弹窗是否显示
    };
  },
  mounted() {
    /**
     * 向列表项追加内容
     */
    for (let i = 0; i < 3; i++) {
      let children = [
        {
          key: `${i}${i}`, //列表key值
          name: `市公安局${i}`, //部门名称
          code: `2111000000${i}`, //部门编码
          division: "市辖区", //行政区划
          phone: "18355661234", //电话号码
          address: "安徽合肥蜀山区" //地址
        }
      ];

      children = [
        {
          key: `${i}${i}1`, //列表key值
          name: `市公安局${i}`, //部门名称
          code: `2111000000${i}`, //部门编码
          division: "市辖区", //行政区划
          phone: "18355661234", //电话号码
          address: "安徽合肥蜀山区", //地址
          children: [
            {
              key: `${i}${i}11`, //列表key值
              name: `市公安局${i}`, //部门名称
              code: `2111000000${i}`, //部门编码
              division: "市辖区", //行政区划
              phone: "18355661234", //电话号码
              address: "安徽合肥蜀山区" //地址
            },
            {
              key: `${i}${i}12`, //列表key值
              name: `市公安局${i}`, //部门名称
              code: `2111000000${i}`, //部门编码
              division: "市辖区", //行政区划
              phone: "18355661234", //电话号码
              address: "安徽合肥蜀山区", //地址
              children: [
                {
                  key: `${i}${i}121`, //列表key值
                  name: `市公安局${i}`, //部门名称
                  code: `2111000000${i}`, //部门编码
                  division: "市辖区", //行政区划
                  phone: "18355661234", //电话号码
                  address: "安徽合肥蜀山区" //地址
                }
              ]
            }
          ]
        },
        {
          key: `${i}${i}2`, //列表key值
          name: `市公安局${i}`, //部门名称
          code: `2111000000${i}`, //部门编码
          division: "市辖区", //行政区划
          phone: "18355661234", //电话号码
          address: "安徽合肥蜀山区" //地址
        },
        {
          key: `${i}${i}3`, //列表key值
          name: `市公安局${i}`, //部门名称
          code: `2111000000${i}`, //部门编码
          division: "市辖区", //行政区划
          phone: "18355661234", //电话号码
          address: "安徽合肥蜀山区" //地址
        }
      ];

      this.data.push({
        key: i, //列表key值
        name: `市公安局${i}`, //部门名称
        code: `2111000000${i}`, //部门编码
        division: "市辖区", //行政区划
        phone: "18355661234", //电话号码
        address: "安徽合肥蜀山区", //地址
        children: children
      });
    }
  },
  methods: {
    customExpandIcon(props) {
      console.log(props);
      if (props.expandable) {
        if (props.expanded) {
          return (
            <a-icon
              type="up"
              onClick={e => {
                props.onExpand(props.record, e);
              }}
            />
          );
        } else {
          return (
            <a-icon
              type="down"
              onClick={e => {
                props.onExpand(props.record, e);
              }}
            />
          );
        }
      } else {
        return <span style={{ marginRight: "0.83vw" }}></span>;
      }
    },
    /**
     * 列表项选中状态
     */
    onSelectChange(selectedRowKeys) {
      console.log("selectedRowKeys changed: ", selectedRowKeys);
      this.selectedRowKeys = selectedRowKeys;
    },
    /**
     * 点击×号关闭弹框
     */
    closeModal() {
      this.addModal = false;
      this.editModal = false;
      this.examineModal = false;
      this.deleteModal = false;
    },
    /**
     * 点击新增打开弹窗
     */
    addMessage() {
      this.addModal = true;
    },
    /**
     * 点击编辑打开弹窗
     */
    editMessage() {
      this.editModal = true;
    },
    /**
     * 点击查看打开弹窗
     */
    getMessage() {
      this.examineModal = true;
    },
    /**
     * 点击删除打开弹窗
     */
    deleteMessage() {
      this.deleteModal = true;
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
